.games_wrap {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 20px 40px;
    margin: 0 auto;
    max-width: 700px;   
}

.game_wrap_title{
    width: 320px;
    background: radial-gradient(50% 50% at 50% 50%,#374529 0,#0f140a 100%);
    display: flex;
    align-items: center;
    padding:5px;
    margin-bottom: 20px;
    max-width:350px ;
    cursor: pointer;
    border-radius: 10px;
}

.game_title{
    font-weight: 600;
    font-size: Min(20px, 4vw);
    margin-left: 20px;
}

.catalog_instruction {
    min-width: 280px;
    max-width: 500px;
    /* min-height: 80px; */
    /* margin: 0 auto; */
    padding: 5px;
    background-color: #fff;
    border-radius: 5px;
    background: radial-gradient(50% 50% at 50% 50%,#374529 0,#0f140a 100%);
    line-height: 120%;
}

.catalog_instruction p:first-child {
    margin-bottom: 10px;
}

.catalog_instruction a:hover {
    border: 1px solid #374529;
}
