.about_container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 20px 40px;
    margin: 0 auto;
    max-width: 700px; 
}

.about_instruction {
    min-width: 280px;
    max-width: 500px;
    /* min-height: 80px; */
    /* margin: 0 auto; */
    padding: 5px;
    background-color: #fff;
    border-radius: 5px;
    background: radial-gradient(50% 50% at 50% 50%,#374529 0,#0f140a 100%);
    line-height: 120%;
}

.about_instruction p {
    margin-bottom: 10px;
}

.about_instruction a:hover {
    border: 1px solid #374529;
}